<template>
  <div class="content">
    <v-content-up-carousel></v-content-up-carousel>
    <v-content-up-banner></v-content-up-banner>
    <br/>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>推荐作品</span>
        <el-button style="float: right; padding: 3px 0" type="text"><router-link :to="0">更多</router-link></el-button>
      </div>
      <el-row>
        <el-col :span="8" v-for="(o) in tjzp.data" :key="o">
          <div class="imgbox">
            <router-link :to="'/article?id=' + o.id"><img :src="o.img" class="image"></router-link>
            <div style="padding: 14px;">
              <div>{{ o.title }}</div>
              <span class="remark">{{ o.remark }}</span>
              <div class="bottom clearfix">
                <time class="time">{{ o.time }}</time>
                <el-button type="text" class="button"><router-link :to="'/article?id=' + o.id">查看详情</router-link></el-button>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
    </el-card>
    <br/>
    <el-row :gutter="16">
      <el-col :span="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>技术文档</span>
            <el-button style="float: right; padding: 3px 0" type="text">更多</el-button>
          </div>
          <div v-for="o in jswd.data.slice(0, maxSize)" :key="o" class="text item">
            <router-link :to="'/article?id=' + o.id"> > {{ o.title }}</router-link>
          </div>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>学习视频</span>
            <el-button style="float: right; padding: 3px 0" type="text">更多</el-button>
          </div>
          <div v-for="o in xxsp.data.slice(0, maxSize)" :key="o" class="text item">
            <router-link :to="'/article?id=' + o.id"> > {{ o.title }}</router-link>
          </div>
        </el-card>
      </el-col>
    </el-row>
    <br/>
    <el-row :gutter="16">
      <el-col :span="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>开源项目</span>
            <el-button style="float: right; padding: 3px 0" type="text">更多</el-button>
          </div>
          <div v-for="o in kyxm.data.slice(0, maxSize)" :key="o" class="text item">
            <router-link :to="'/article?id=' + o.id"> > {{ o.title }}</router-link>
          </div>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>开发工具</span>
            <el-button style="float: right; padding: 3px 0" type="text">更多</el-button>
          </div>
          <div v-for="o in kfgj.data.slice(0, maxSize)" :key="o" class="text item">
            <router-link :to="'/article?id=' + o.id"> > {{ o.title }}</router-link>
          </div>
        </el-card>
      </el-col>
    </el-row>
    <v-scroll></v-scroll>
  </div>
</template>

<script>
import vContentUpCarousel from '../page/ContentUpCarousel.vue'
import vContentUpBanner from '../page/ContentUpBanner.vue'
import { tjzp, jswd, xxsp, kyxm, kfgj } from '../../../static/js/article.js'
import vScroll from '../common/Scroll.vue'

export default {
  data () {
    return {
      maxSize: 4,
      currentDate: '2019-05-01',
      tjzp: tjzp,
      jswd: jswd,
      xxsp: xxsp,
      kyxm: kyxm,
      kfgj: kfgj,
      trigger: false
    }
  },
  components: {
    vContentUpCarousel, vContentUpBanner, vScroll
  }
}
</script>

<style scoped>
.bg-purple {
  background: #d3dce6;
}
.box-card .img {
  width: 200px;
  height: 80px;
  overflow:hidden
}
.text {
  font-size: 14px;
}
.item {
  margin-bottom: 18px;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both
}
/* 卡片图片 */
.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  height: 200px;
  display: block;
}

.clearfix:before,
.clearfix:after {
    display: table;
    content: "";
}

.clearfix:after {
    clear: both
}
.imgbox {
  background:white;
  margin: 5px;
  padding: 10px;
  border: 1px solid rgb(250, 244, 244);
}
.imgbox:hover{
  /* transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -o-transform: scale(1.1);
  -ms-transform: scale(1.1);
  border:1px solid #D94600; */
  -webkit-box-shadow: 2px 2px 8px 1px rgba(20, 20, 20, 0.15);
  -moz-box-shadow: 2px 2px 8px 1px rgba(20, 20, 20, 0.15);
  box-shadow: 2px 2px 8px 1px rgba(20, 20, 20, 0.15);
}
.remark {
  margin-top: 10px;
  font-size: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
</style>



